<template>
  <el-form
    :inline="true"
    :model="formInline"
    :rules="rules"
    ref="ruleForm"
    class="demo-form-inline"
  >
    <el-form-item label="" prop="SN" :rules="rules.SN">
      <el-input v-model="formInline.SN" placeholder=""></el-input>
    </el-form-item>
    <el-form-item label="MAC" prop="mac" :rules="rules.MAC">
      <el-input v-model="formInline.mac" placeholder="MAC"></el-input>
    </el-form-item>
    <el-form-item label="">
      <el-input v-model="formInline.serial" placeholder=""></el-input>
    </el-form-item>
    <el-form-item label="">
      <el-select v-model="formInline.type" placeholder="">
        <el-option label="" value></el-option>
        <el-option label="" value="ac"></el-option>
        <el-option label="" value="ml_switch"></el-option>
        <el-option label="" value="ml_router"></el-option>
        <el-option label="" value="h_switch"></el-option>
        <el-option label="" value="h_router"></el-option>
        <el-option label="" value="securitypro"></el-option>
        <el-option label="" value="ap"></el-option>
      </el-select>
    </el-form-item>
    <el-form-item label="">
      <el-select v-model="formInline.site" placeholder="">
        <el-option label="" value></el-option>
        <el-option label="SETMAC" value="SETMAC"></el-option>
        <el-option label="" value=""></el-option>
        <el-option label="1" value="1"></el-option>
        <el-option label="" value=""></el-option>
        <el-option label="1" value="1"></el-option>
        <el-option label="" value=""></el-option>
        <el-option label="" value=""></el-option>
        <el-option label="" value=""></el-option>
        <el-option label="OQC" value="OQC"></el-option>
        <el-option label="" value=""></el-option>
        <el-option label="" value=""></el-option>
      </el-select>
    </el-form-item>
    <el-form-item label="">
      <el-select v-model="formInline.result" placeholder="">
        <el-option label="" value></el-option>
        <el-option label="" value="PASS"></el-option>
        <el-option label="" value="FAIL"></el-option>
      </el-select>
    </el-form-item>
    <el-form-item label="">
      <el-date-picker
        v-model="formInline.dateRange"
        type="datetimerange"
        align="right"
        unlink-panels
        range-separator=""
        start-placeholder=""
        end-placeholder=""
        :picker-options="pickerOptions"
      ></el-date-picker>
    </el-form-item>
    <el-form-item>
      <el-button type="primary" @click="submitForm('ruleForm')"></el-button>
    </el-form-item>
  </el-form>
</template>
<script>
export default {
  data() {
    return {
      formInline: {
        SN: '',
        user: '',
        region: '',
        dateRange: [],
      },
      rules: {
        SN: [
          {
            validator(rule, value, callback) {
              if (!value || value.length === 13) {
                callback();
              } else {
                callback('13');
              }
            },
            trigger: 'blur',
          },
        ],
        MAC: [
          {
            validator(rule, value, callback) {
              if (!value || value.length === 12) {
                callback();
              } else {
                callback('12MAC');
              }
            },
            trigger: 'blur',
          },
        ],
      },
      pickerOptions: {
        shortcuts: [
          {
            text: '',
            onClick(picker) {
              const end = new Date();
              const start = new Date();
              start.setTime(start.getTime() - 3600 * 1000 * 24 * 7);
              picker.$emit('pick', [start, end]);
            },
          },
          {
            text: '',
            onClick(picker) {
              const end = new Date();
              const start = new Date();
              start.setTime(start.getTime() - 3600 * 1000 * 24 * 30);
              picker.$emit('pick', [start, end]);
            },
          },
          {
            text: '',
            onClick(picker) {
              const end = new Date();
              const start = new Date();
              start.setTime(start.getTime() - 3600 * 1000 * 24 * 90);
              picker.$emit('pick', [start, end]);
            },
          },
        ],
      },
    };
  },
  methods: {
    submitForm(formName) {
      this.$refs[formName].validate((valid) => {
        if (valid) {
          const {
            SN,
            mac,
            type,
            site,
            serial,
            result,
          } = this.$data.formInline;
          let { dateRange } = this.$data.formInline;
          // fix dateRange is null
          dateRange = dateRange || [];
          const [start, end] = dateRange;
          const parent = this.$parent.$parent.$parent;

          parent.setSearchParams({
            SN: SN && SN.trim(),
            MAC: mac && mac.trim(),
            Product_Model: serial && serial.trim(),
            Product_Type: type,
            Test_Station: site,
            TestResult: result,
            StartTime: start && start.valueOf(),
            EndTime: end && end.valueOf(),
          });
          return true;
        }
        return false;
      });
    },
  },
};
</script>
<style>
.demo-form-inline .el-input {
  width: 150px;
}
.demo-form-inline .el-select>.el-input {
  width: 150px;
}
</style>
